/* elements */

body {
  margin: 0;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: "Questrial", "Roboto", "Raleway", "Monteserrat";
  font-size: 1.1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  color: #7f7f7f;
}

html {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

/* classes */

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-content {
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-gutter: stable;
}

.certificate {
  height: 200px;
}

.global-footer {
  font-size: 15px;
  padding-left: 20px;
  bottom: 0;
  flex: none;
}

.img-thumbnail {
  height: 100%;
  overflow: hidden;
  margin-left: 15px;
  margin-right: 15px;
}

.img-thumbnail:hover > div > .thumbnail-image {
  transform: scale(1.05);
}

.img-content {
  width: 100%;
}

.jumbotron {
  opacity: 0.9;
  font-size: 1.3rem;
}

.jumbotron h1 {
  font-size: 4rem;
}

.page-content {
  padding: 1.5% 3% 1%;
}

.nav-item {
  color: #ffffff;
}

.navbar {
  flex: none;
}
.resume-subsection {
  margin-left: 1%;
  margin-right: 1%;
}
.search-bar-container {
  width: 100%;
  display: flex;
  padding-left: 30px;
  padding-right: 30px;

  & .search-bar-input {
    width: 100%;
    border: 1px solid #dee2e6;
    border-radius: 0.25rem;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
  }
}

.thumbnail {
  margin-top: 15px;
  margin-bottom: 20px;
  padding-left: 15px;
  padding-right: 15px;
}

.thumbnail-container {
  display: flex;
  flex-wrap: wrap;
  & .no-projects-warning {
    flex-grow: 1;
    text-align: center;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.thumbnail-image {
  width: 100%;
}

.thumbnail-image-container {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
/* ids */

#about-background {
  background-image: url("./pictures/background.gif");
  background-position: 25% 30%;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1 1 auto;
  padding-top: 7%;
  padding-right: 15%;
}
